
import common from '@/libs/common.mixin'
import globals from '@/libs/globals.mixin'

import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'


export default{
mixins: [ common, globals ],
components: {
Tab,
TabItem,
Swiper,
SwiperItem
},
data () {
return {
tab: 0,
data: {0: [],1: []},
map_absence_status: {0:'未补课',1:'已安排',2:'已补课'}
}
},
computed: {
data_uri () {
if(this.tab==0){
return 'student_absences?status=[In,0,1]&with=one_class&sid='+this.sid$
}
return 'makeup_arranges?with=one_class,absence,leave&sid='+this.sid$
},
swiperHeight () {
let height = window.innerHeight
return (height - this.headerHeight - 44) +'px'
}
},
methods: {
makeup (item) {
this.$router.push({path:'./add_appointment',query:{sj_id: item.sj_id, sa_id:item.sa_id}})
},
getItemTitle (item) {
let result = ''
if(item.cid > 0){
result = item.one_class.class_name
}else{
result = this.$filter('lesson_name')(item.lid)
}
return result
},
deal_data (response) {
let list = response.body.data.list,
total = response.body.data.total,
tab = this.tab
if(this.pageIndex===1){
this.data[tab] = []
}

if(list.length>0){
this.data[tab] = this.data[tab].concat(list)
}

this.mescroll.endBySize(list.length,total)
}
}
}



